.Navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, .25);
    width: 90%;
    height:80px;
    border-radius: 13px;
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
    z-index: 9999;
    background-color: #fff;
}

.logo{
    color: black;
    font-size: 2rem;
    cursor: pointer;
}

.menu{
    display: grid;
    grid-template-columns: repeat(6,auto);
    list-style: none;
    align-items: center;
    grid-gap: 10px;
}

.nav-links{
    text-decoration: none;
    color: black;
    font-size: 1.2rem;
    font-weight: 600;
    padding: 0.7rem 1rem;
    white-space: nowrap;
}

.nav-links i{
    margin-right: 10px;
}

.nav-links:hover{
    background:linear-gradient(to right,rgb(41, 169, 224),rgb(150, 150, 150));
    color: #fff;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
}

.btn{
    padding: 0.5rem 1rem;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    
}

.btn:hover{
    color: #fff;
    background-color: black;
}

.menu-icons{
    display: none;
}

@media screen and (max-width:850px) {
    .Navbar{
        z-index: 99;
    }
    .menu{
       display: flex;
       flex-direction: column;
       justify-content: flex-start;
       background-color:#fff;
       width: 100%;
       height: auto;
       position: absolute;
       top: 0;
       left: -100%;
       align-items: center;
       opacity: 0;
       padding: 80px 0 30px 0;
       margin: 0;
       transition: 0.3s ease-in-out;
  
    }
    .menu.active{
        left: 0;
        opacity: 1;
        z-index: -1;
        transition: 0.3s ease-in-out;
    }

    .nav-links{
        display: block;
        width: 100%;
        font-size: 1.2rem;
        padding: 2rem 0;
    }
    .nav-links:hover{
        background:linear-gradient(to right,rgb(41, 169, 224),rgb(150, 150, 150));
        transition: none;
        width: 500px;
        text-align: center;
        border-radius: 13px;
    }

    .btn{
        display: none;
    }

    .menu-icons{
        display: block;
        cursor: pointer;
    }
    .menu-icons i{
        color: black;
        font-size: 1.2rem;
    }
    .nav-links-mobile{
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: auto;
        border-radius: 13px;
        width: 80%;
        background:linear-gradient(to right,rgb(41, 169, 224),rgb(150, 150, 150));
        text-decoration: none;
        color: #fff;
        font-size: 1.2rem;
        font-weight: 700;
        letter-spacing: 2px;
        
    }
}
